import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import Video from '@ui/video';
//import Video from '@ui/video';


const Intro = () => (
  <StaticQuery
    query={graphql`
      query {
        Earphone4all: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Allcolour: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "allcolour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Noise: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "noise" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "parkour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro">
        <SectionHeader 
          header="The first earphone design for all hearing conditions" 
          subheader="We're launching a campaign to create a new, innovative earphone designed for you — whether you are normal hearing, a hearing aid user, or a cochlear implant user."
          toggle2Act="true"
          text="Visit our Indiegogo pre-launch page!"
          href="https://www.indiegogo.com/projects/hai-world-s-first-bluetooth-earphones-for-all/coming_soon/x/23571765"
          target="_blank"
        />
        <Video videoSrcURL="https://www.youtube.com/embed/OpiWKbHBuAU" videoTitle="main video"/>
        <ImgTxt
          header="Perfect solution for many hearing aid and cochlear implant users" 
          text="It’s not likely that any style of earbuds or earphones are going to work with hearing aids. The reason is quite straightforward — hearing aids already sit within ears, so fitting another device into the ears isn't possible."   
        >
          <Img fluid={data.Allcolour.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Universal and modular design" 
          text="Rearia Hai is the first earphone that is universal for all hearing aid users, cochlear implant users, and hearing people. Thanks to its modular design with neckcoil technology, you can wear it no matter whether you wear bilateral aids, single-sided, or just no aids at all. You can quickly and easily switch between the earbuds and coils and mix them as you want." 
        >
          <Img fluid={data.Unidesign.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgTxt 
          header="Experience superior sound quality" 
          text="Telecoil technology is an established technology which is compatible with most hearing aids and cochlear implants. We developed this versatile technology further to provide outstanding sound quality for enjoying music."
        >
          <Img fluid={data.Earphone4all.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Stop the buzz" 
          text="For even further improved listening experience, we developed the first technology to cancel any bothering interference from nearby electrical infrastructure. We call it DBC (Dynamic Buzz Cancellation). It lets you enjoy music free from buzzing in difficult environments — such as in a plane or near power systems." 
        >
          <Img fluid={data.Noise.childImageSharp.fluid} />
        </ImgTxtInverse>   
      </Section>
    )}
  />
);

export default Intro;
