import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import ImgSingle from '@ui/img_single';
import ShortChannelling from '@common/ShortChannelling';
//import Video from '@ui/video';


const Intro2 = () => (
  <StaticQuery
    query={graphql`
      query {
        Earphone4all: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Music: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "music" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Noise: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "noise" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "parkour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro" padding="tight">
        <SectionHeader 
          header="We provide even more!" 
        />
        <ImgSingle
          header="Adjust with the Hai App" 
          text="Change your preferred equalizer settings and save them to suit all situations. Let speech stand out clearer when making a phone call. It also gives guidance to change the stereo balance of your phone to fit your hearing condition." 
          togglecover="true"  
          toggletop="false"       
        >
           <Img fluid={data.Mobileeq.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="Intuitive and seamless usage" 
          text="Convenient design for easy usage with three buttons operation — answer calls, change tracks and adjust volume easily. You can also talk to your Google Assistant or Siri with Hai for seamless integration with your smartphone."    
        >
          <Img fluid={data.Ui.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Clear conversations" 
          text="Call your loved ones, friends or business clients with the built-in microphone — integrated with technology which provides clear voice and low latency." 
        >
          <Img fluid={data.Conversation.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgSingle
          header="Express your style" 
          text="Customize your Hai with different coloured surfaces and stickers! Let your creativity run free and let your Hai show your style."
          togglecover="true"  
          toggletop="false"       
        >
          <Img fluid={data.Stickers.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="With you anywhere, anytime" 
          text="Whatever the weather might be, you can bring Hai anywhere with you. Lightweight design allows it to easily clip to your clothes — or simply wear it like a necklace! Water resistant and modern design lets you wear it while exercising, working or just relaxing and enjoying music."  
        >
          <Img fluid={data.Anywhere.childImageSharp.fluid} />
        </ImgTxt>  
        <ShortChannelling/>
      </Section>
    )}
  />
);

export default Intro2;
